h2 {
    margin-bottom: 1em;
}
.sectionOneContainer {
    display: flex;
    justify-content: center;
    line-height: 170%;
}

.sectionOneText {
    padding: 5%;
    background-color: #ABB7B7;
    max-width: 60%;
}

.sectionOneTextMobile {
    padding: 5%;
    background-color: #ABB7B7;
    max-width: 100%;
}

.sectionTwoText {
    padding: 5%;
    background-color: white;
    line-height: 170%;
    max-width: 50%;
}

.sectionTwoTextMobile {
    padding: 5%;
    background-color: white;
    line-height: 170%;
    max-width: 100%;
    text-align: center;
}

.sectionTwoFiller {
    display: flex;
    justify-content: center;
    background-color: white;
    line-height: 170%;
    width: 50%;
}

.sectionThreeContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5%;
    background-color: #ABB7B7;
    line-height: 170%;
    max-width: 100%;
}

.sectionThreeText {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 60%;
    margin-top: 1em;
}

.sectionThreeTextMobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    margin-top: 1em;
}

.image {
    object-fit: cover;
    width: 40%;
}

.imageMobile {
    object-fit: cover;
    max-width: 100%;
}

.flex {
    display: flex;
}

.flexColumn {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.desktopTitle {
    font-family: 'Lato';
    font-weight: lighter;
    margin-left: 5%;
}

#title {
    font-size: 35px;
}
.desktopNav {
    height: 20vh;
    background-color: #ECF0F1;
    color: #2980B9;
    max-width: 100%;
}

.mobileNavContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 100%;
}

.mobileTitle {
    font-size: 35px;
    font-family: 'Lato';
    font-weight: lighter;
    text-align: center;
}

.mobileNav {
    height: 20vh;
    background-color: #ECF0F1;
    color: #2980B9;
    display: flex;
    justify-content: center;
    align-items: center;
}


.servicesSectionOne {
    background-color: #ABB7B7;
    height: 60vh;
    width: 60vw;
    padding: 5%;
    line-height: 170%;
    font-size: 110%;
}

.servicesSectionOneFiller {
    background-color: white;
    height: 60vh;
    width: 40vw;
}

.servicesSectionTwo {
    display: flex;
    background-color: #ABB7B7;
    padding: 3%;
}

.medicare {
    background-color: #FFFF7E;
    height: 80vh;
    width: 50%;
    margin-right: 1%;
    padding: 5%;
    display: flex;
    flex-direction: column;
    align-items: left;
}

.more {
    background-color: #FFFF7E;
    height: 80vh;
    width: 50%;
    padding: 5%;
    margin-left: 1%;
    display: flex;
    flex-direction: column;
    align-items: left;
    border-left: 3% solid #ABB7B7;
}

.servicesSectionOneMobile {
    background-color: #ABB7B7;
    line-height: 160%;
    font-size: 100%;
    height: 200%;
    max-width: 100%;
    padding: 5%;
    display: flex;
    flex-direction: column;
    align-items: right;
}


.servicesSectionTwoMobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ABB7B7;
}

.medicareMoreH2 {
    text-align: center;
}

.medicareMobile {
    background-color: #FFFF7E;
    height: 150%;
    width: 90%;
    padding: 5%;
    display: flex;
    flex-direction: column;
    align-items: left;
    margin-bottom: 3%;
}

.moreMobile {
    background-color: #FFFF7E;
    height: 150%;
    width: 90%;
    padding: 5%;
    display: flex;
    flex-direction: column;
    align-items: left;
    margin-bottom: 3%;
}

.contactFlexContainer {
    display: flex;
    justify-content: center;
}

.contactTitle {
    display: flex;
    justify-content: center;
    margin-top: 5%;
}
.contactInput {
    height: 8vh;
    border-color: #2c3e50;
}

.contactSubmit {
    background-color: #2ecc71;
    color: white;
    height: 20%;
    width: 20vh;
    font-size: 100%;
    margin-bottom: 5%;
    margin-top: 5%;
}

.gotcha {
    display: none;
}

.textArea {
    height: 24vh;
    border-color: #2c3e50;
}

.contactInputMobile {
    height: 8vh;
    border-color: #2c3e50;
    width: 100vw;
}

.contactSubmitMobile {
    background-color: #2ecc71;
    color: white;
    height: 20%;
    width: 100%;
    font-size: 100%;
    margin: 1%;
}

.textAreaMobile {
    height: 24vh;
    border-color: #2c3e50;
    width: 100%;
}

.form {
    padding: 5%;
    width: 100%;
}

.contact {
    width: 100vw;
}